import cssVar from "views/mobileView/styles"

const RiskFormat = (riskID) => {
    switch(riskID){
        case 1 : return'Super conservative'
        case 2 : return'Conservative'
        case 3 : return'Moderate'
        case 4 : return'Aggressive'
        case 5 : return'Super aggressive'
        case 6 : return'Very aggressive'
        default: return ''
    }
}

const RiskColor = (riskID) => {
    switch(riskID){
        case 1 : return cssVar.getPropertyValue('--lowestRisk')
        case 2 : return cssVar.getPropertyValue('--conservativeRisk')
        case 3 : return cssVar.getPropertyValue('--moderateRisk')
        case 4 : return cssVar.getPropertyValue('--secondHeighestRisk')
        case 5 : return cssVar.getPropertyValue('--highestRisk')
        case 6 : return cssVar.getPropertyValue('--highestRisk')
        default: return ''
    }
}

const RiskTextColor = (riskID) => {
    switch(riskID){
        case 1 : return '#3395FF'
        case 2 : return cssVar.getPropertyValue('--darkGreen')
        case 3 : return cssVar.getPropertyValue('--gold')
        case 4 : return cssVar.getPropertyValue('--internationalEquity')
        case 5 : return '#FF5A5F'
        case 6 : return cssVar.getPropertyValue('--highestRisk')
        default: return ''
    }
}
export {RiskFormat,RiskColor,RiskTextColor}