import React from 'react';
import cssVar from "views/mobileView/styles";
import Tabs from 'views/mobileView/components/atoms/Tabs';
import Highcharts from 'highcharts'
import HighchartsReact from "highcharts-react-official";
import { CurrencyFormat } from '../../atoms/CurrencyFormat';
import { BiRefresh } from 'react-icons/bi';
import RedGreenText from '../../atoms/RedGreenText';

const HomePortfolioCard = ({ balance = 0, previousBalance = 0, xirr = 0, onRefresh = () => { }, growthChartData = [], pieChartData , type, finalCorpus}) => {
  // xirr = -12
  let change = 0;
  let changePercentage = 0;
  if(previousBalance !== 0 && previousBalance !== ''){
    change = balance-previousBalance;
    changePercentage = change*100/previousBalance;
  }
  const value = () => {
    return (
        <div style={{ display: "flex",padding: "4%" }}>
          <div style={{ flex: 1 }}>
            <div className='bigHeader' style={{display:'flex',alignItems:'center',gap:"10px"}}>
              {CurrencyFormat(balance)} <BiRefresh size={24} className='secondaryGreen' onClick={onRefresh} />
            </div>
            <div style={{display:'flex',gap:'4px'}}>
              <span className='smallTextLight'>1 Day</span>
              <span className='smallTextDark'>
                <RedGreenText text={CurrencyFormat(change,'long',2)} value={change}/>{' '}
                <RedGreenText text={"(" + changePercentage.toFixed(2) + "%)"} value={changePercentage}/>
              </span>
            </div>
          </div>
          <div style={{textAlign:'right'}}>
            <div className='bodyTextLight'>
              Annualized return
            </div>
            <div className='header1dark'>
              <RedGreenText text={xirr.toFixed(2)+'%'} value={xirr}/>
            </div>
          </div>
        </div>
    )
  }

  const growth = () => {
    const options = {
      chart: {
        backgroundColor: 'transparent',
        height: 220,
      },
      boost: {
        useGPUTranslations: true,
        seriesThreshold: 5
      },

      title: {
        text: ''
      },
      exporting: {
        enabled: false
      },
      responsive: {
        rules: [{
          condition: {
            maxHeight: 100
          }
        }]
      },
      yAxis: {
        title: {
          text: ''
        },
        // min: Math.min.apply(Math, growthChartData.map(o => o[1])),
        startOnTick: true
        // visible: false
      },

      xAxis: [{
        type: 'datetime',
        dateTimeLabelFormats: {
          millisecond: '%H:%M:%S.%L',
                    second: '%H:%M:%S',
                    minute: '%H:%M',
                    hour: '%H:%M',
                    day: '%eth %b',
                    week: '%eth %b',
                    month: '%b \'%y',
                    year: '%Y'
},

        labels: {
          step:2
        },
        crosshair: {
          width: 1,
          color: 'grey'
        },
        // endOnTick: true,

        //  categories: [''],
        // visible: false
      }
      ],

      legend: {
        enabled: false
      },

      plotOptions: {
        series: {
          label: {
            connectorAllowed: false
          },
        }
      },
      plotOptions: {
        series: {
          turboThreshold: 51000,//larger threshold or set to 0 to disable
          marker: {
            enabled: false
          }
        }

      },
      tooltip: {
        shared: true
      },
      credits: {
        enabled: false
      },
      scrollbar: {
        enabled: false
      },
      series: [{
        name: 'Balance',
        color: cssVar.getPropertyValue('--darkGreen'),
        data: growthChartData
      }],
      responsive: {
        rules: [{
          condition: {
            maxWidth: 400
          },
        }]
      }
    };
    const optionsGoal = {
      chart: {
          backgroundColor: 'transparent',
  height: 220,
      },
      boost: {
          useGPUTranslations: true,
          seriesThreshold: 5
      },
      title: {
          text: ''
      },
      exporting: {
          enabled: false
      },
      yAxis: {
          title: {
              text: ''
          },
          plotLines: [{
              color: '#000',
              value: Number(finalCorpus), // Position, you'll have to translate this to the values on your x axis
              dashStyle: 'dash',
              width: 1,
              label: {
                  text: "Target:- " + (CurrencyFormat(Number(finalCorpus),'short',2)), y: -5, x: -2,
                  style: {
                      color: cssVar.getPropertyValue('--secondaryGreen'),
                      fontWeight: 'bold'
                  }
              }
          }]
      },
      xAxis: [{
          type: 'datetime',
          dateTimeLabelFormats: {
              millisecond: '%H:%M:%S.%L',
              second: '%H:%M:%S',
              minute: '%H:%M',
              hour: '%H:%M',
              day: '%eth %b',
              week: '%eth %b',
              month: '%b \'%y',
              year: '%Y'
          },
          crosshair: {
              width: 1,
              color: 'grey'
          },
          endOnTick: true,
          startOnTick: true,
      }
      ],
      legend: {
          enabled: false
      },
      plotOptions: {
          series: {
              label: {
                  connectorAllowed: false
              },
          }
      },
      plotOptions: {
          series: {
              turboThreshold: 51000,//larger threshold or set to 0 to disable
              marker: {
                  enabled: false
              }
          }
      },
      tooltip: {
          valueDecimals: 0,
          valuePrefix: '₹',

      },
      credits: {
          enabled: false
      },
      series: growthChartData,
      scrollbar: {
          enabled: false
      },
      responsive: {
          rules: [{
              condition: {
                  maxWidth: 400
              },
          }]
      },

  };
    return (
      <div style={{ padding: "4%" }}>
        <HighchartsReact
          highcharts={Highcharts}
          options={type==='goal'?optionsGoal:options}
          />
        
      </div>)
  }
  const asset = () => {
    var options = {
      chart: {
        type: 'pie',
        style: {
          fontFamily: 'DMSans-Regular',
          color: '#333',
          fontWeight: 'normal'
        },
        backgroundColor: "transparent",
        plotBackgroundColor: null,
        plotBorderWidth: null,
        plotShadow: false,
        height: 220
      },
      title: {
        text: ''
      },
      scrollbar: {
        enabled: false
      },
      exporting: {
        enabled: false
      },
      legend: {
        itemStyle: {
          color: "#6c6c6c",
          fontSize: 14,
          fontWeight: 'normal',
          fontFamily: 'DMSans-Regular'
        }
      },

      plotOptions: {
        pie: {
          allowPointSelect: true,
          cursor: 'pointer',
          dataLabels: {
            enabled: false
          },

          showInLegend: true,
          size: 150,
        }
      },
      credits: {
        enabled: false
      },
      tooltip: {
        valueSuffix: '%'
      },
      legend: {
        enabled: true,
        align: 'right',
        verticalAlign: 'top',
        layout: 'vertical',
        x: -30,
        y: 30,
        itemMarginBottom: 10
      },
      series: [
        {
          data: pieChartData
        }
      ]
    };
    return (
      <div style={{ padding: "4%" }}>
        <HighchartsReact
          highcharts={Highcharts}
          options={options}
        />
      </div>)
  }
  const keyTitles = [
    { title: type==='goal'?"Corpus":"Value", view: value(), show: true },
    { title: type==='goal'?"Projected journey":"Growth", view: growth(), show: true },
    { title: "Asset allocation", view: asset(), show: pieChartData !== undefined }
  ];

  return (
    <div style={{ backgroundColor: cssVar.getPropertyValue('--lighter'), border: "1px solid "+cssVar.getPropertyValue('--light'), borderRadius: '8px', margin: "4%" }}>
      <Tabs tabsObject={keyTitles.filter(k => k.show)} color={cssVar.getPropertyValue('--darkGreen')}/>
    </div>
  )
};

export default HomePortfolioCard;
