// import { Box, Tab, Tabs } from '@mui/material';
import React from 'react';
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import { useSwipeable } from 'react-swipeable'

function TabBar({ tabsObject, color, defaultTab = 0}) {
  const [activeKey, setActiveKey] = React.useState(defaultTab);

  const handlers = useSwipeable({
    // onSwipedLeft: (e) => handelLeftSwipe(),
    // onSwipedRight: (e) => handelRightSwipe(),
    trackMouse: true,
    trackTouch: true,
    preventDefaultTouchmoveEvent: true
  })
  const handelLeftSwipe = () => {
    if (activeKey === tabsObject.length - 1) {
      setActiveKey(0)
    }
    else setActiveKey(activeKey + 1)
  }
  const handelRightSwipe = () => {
    if (activeKey === 0) {
      setActiveKey(tabsObject.length - 1)
    }
    else setActiveKey(activeKey - 1)
  }

  return (
    <div {...handlers}>
      {
        color ?
          <style type="text/css">
            {`
              .nav-tabs.normal-tabs .nav-link.active {
                color: ${color} !important;
                border-bottom: 3px solid ${color} !important;
              }
            `}
          </style>
          :
          ''
      }
      <Tabs activeKey={activeKey} onSelect={(k) => setActiveKey(k * 1)} className='normal-tabs'>
        {tabsObject.map((t, i) => {
          return (<Tab key={i} eventKey={i} title={t.title}>{t.view}</Tab>)
        })}
      </Tabs >
    </div>
  );
}
export default TabBar