import React from 'react';
import { CurrencyFormat } from '../../../../components/atoms/CurrencyFormat';
import { AiFillCaretDown, AiFillCaretUp,AiOutlineRight } from "react-icons/ai";
import MintboxIcon from 'views/mobileView/components/atoms/MintboxIcon';
import RedGreenText from 'views/mobileView/components/atoms/RedGreenText';
import cssVar from 'views/mobileView/styles';
import AMCIcon from 'views/mobileView/components/atoms/AMCIcon';

const FundCard = ({ fundName,risk=0,AMCName, balance=0, previousBalance=0 , xirr=0, goalName = '',onClick, type=3 }) => {
  
  let change = 0;
  let changePercentage = 0;
  if(previousBalance !== 0){
    change = balance-previousBalance;
    changePercentage = change*100/previousBalance;
  }
  return (
    <div style={{padding:"4%",borderBottom:"1px solid "+cssVar.getPropertyValue('--light'),display:"flex",gap:"4%"}} onClick={()=>onClick()}>
      {
        type === 1 || type === 2 ?
          <AMCIcon amcName={AMCName} height='40px' width='40px' />
          :
          <MintboxIcon risk={risk} height='40px' width='40px' />
      }
      <div style={{flex:"1"}}>
        <div className='header2dark' style={{marginBottom:"2%"}}>{fundName}</div>
        <div className='smallTextLight'>
          <span>1 Day </span>
          {
            change >0?
              <span style={{color:cssVar.getPropertyValue('--secondaryGreen')}}><AiFillCaretUp size={20} style={{verticalAlign:"bottom"}}/> {CurrencyFormat(change,"long",2)} ({changePercentage.toFixed(2)}%)</span>
              :
            change < 0?
              <span style={{color:cssVar.getPropertyValue('--red')}}><AiFillCaretDown size={20} style={{verticalAlign:"bottom"}}/> {CurrencyFormat(change,"long",2)} ({changePercentage.toFixed(2)}%)</span>
              :
              <span> {CurrencyFormat(change,"long",2)} ({changePercentage}%)</span>
          }
        </div>
        {/* {
          goalName !== ''?
          <div className='smallTextLight' style={{marginTop:'2%'}}><HiLink size={14} className="secondaryGreen"/> {goalName}</div>
          : ''
        } */}
      </div>
      <div>
        <div className='header2dark' style={{marginBottom:"4px"}}>
          {CurrencyFormat(balance,"short")}<AiOutlineRight size={14} className='secondaryGreen' style={{marginLeft:"4px"}}/>
        </div>
        <div className='bodyTextLight'>
          <RedGreenText text={xirr.toFixed(2)+'%'} value={xirr}/>
        </div>
      </div>
    </div>
  );
};

export default FundCard;
