import cssVar from "views/mobileView/styles"

const RedGreenText = ({text,value}) => {
    if(Number(value)>0){
        return <span className="secondaryGreen">{text}</span>
    } else if(Number(value)<0){
        return <span style={{color:cssVar.getPropertyValue('--red')}}>{text}</span>
    } else return <span>{text}</span>
}

export default RedGreenText;