import React from "react";
import { toast } from "react-toastify";
import db from "libs/db";
import AdminServices from "services/api/AdminServices";
import FundCard from "views/mobileView/screens/mintboxpro/PortfolioScreen/cards/FundCard";
import MainHeader from "views/mobileView/components/molecules/Headers/MainHeader";
import Tabs from 'views/mobileView/components/atoms/Tabs';
import HomePortfolioCard from "views/mobileView/components/molecules/Cards/HomePortfolioCard";
import cssVar from "views/mobileView/styles";
import { AiOutlineRight } from "react-icons/ai";
import ProgressBar from 'react-bootstrap/ProgressBar';
import { CurrencyFormat } from "views/mobileView/components/atoms/CurrencyFormat";
import jwt_decode from "jwt-decode";
import { initializeSmallCase, updateSmallCaseAuthId } from "services/smallcase";
import ActivityLoaderMintbox from "views/mobileView/components/atoms/ActivityLoaderMintbox";
import NotFound from "./cards/NotFound";
import GoalIcon from "views/mobileView/components/atoms/GoalIcon";


const PortfolioScreen = (props) => {
  const [portfolio, setPortfolio] = React.useState({});
  const [portfolioPerformance, setPortfolioPerformance] = React.useState({});
  const [assetAllocation, setAssetAllocation] = React.useState([]);
  const [portfolioXIRR, setPortfolioXIRR] = React.useState({});

  const [user, setUser] = React.useState({});
  const [isDematLinked, setIsDematLinked] = React.useState(false);

  const [loading, setLoading] = React.useState(true);

  React.useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    setLoading(true)
    try {
      db.get(db.model.user).then(async (p) => {
        setUser(p.userData);
        await getClientData(p.userData.id)
        await getPortfolio(p.userData.id)
        await getPortfolioXIRR(p.userData.id)
        await getPortfolioPerformance(p.userData.id)
        await getPortfolioAssetAllocation(p.userData.id)
        setLoading(false)
      })
    } catch (error) {
      toast.error(error);
      setLoading(false)
    }
  };
  const getClientData = async (id) => {
    await db.get(db.model.clientData).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getClient(id).then(res => res);
        if (res.type === true) {
          const a = JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get Client API :", aa)
          setIsDematLinked(aa.SmallcaseInfo.length !== 0)
          db.set(db.model.clientData, JSON.stringify(aa))
        }
      } else {
          setIsDematLinked(p.SmallcaseInfo.length !== 0)
      }
    })
  };
  const getPortfolio = async (id) => {
    await db.get(db.model.portfolio).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolio(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio API :", aa)
          setPortfolio(aa)
          db.set(db.model.portfolio, JSON.stringify(aa))
        }
      } else {
        setPortfolio(p)
      }
    })
  }

  const getPortfolioXIRR = async (id) => {
    await db.get(db.model.portfolioXirr).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolioXIRR(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          const aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio XIRR API :", aa)
          setPortfolioXIRR(aa)
          db.set(db.model.portfolioXirr, JSON.stringify(aa))
        }
      } else {
        setPortfolioXIRR(p)
      }
    })
  }

  const getPortfolioPerformance = async (id) => {
    await db.get(db.model.portfolioPerformance).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolioPerformance(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolioPerformance API :", aa)
          setPortfolioPerformance(aa)
          db.set(db.model.portfolioPerformance, JSON.stringify(aa))
        }
      } else {
        setPortfolioPerformance(p)
      }
    })
  }

  const getPortfolioAssetAllocation = async (id) => {
    await db.get(db.model.assetAllocation).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolioAssetAllocation(id).then(res => res);
        if (res.type === true) {
          const aa = res.data;
          console.log("Get portfolio asset allocation API :", aa)
          formatAssetAllo(aa)
          db.set(db.model.assetAllocation, JSON.stringify(aa))
        }
      } else {
        formatAssetAllo(p)
      }
    })
  }
  const formatAssetAllo = (aa) => {
    const assetClasses = [
      {
        name: 'Indian Equity',
        color: cssVar.getPropertyValue('--indianEquity')
      },
      {
        name: 'Gold',
        color: cssVar.getPropertyValue('--gold')
      },
      {
        name: 'US Equity',
        color: cssVar.getPropertyValue('--internationalEquity')
      },
      {
        name: 'Debt',
        color: cssVar.getPropertyValue('--debt')
      },
      {
        name: 'Cash',
        color: cssVar.getPropertyValue('--cash')
      },
      {
        name: 'Silver',
        color: cssVar.getPropertyValue('--silver')
      }
    ];
    let percentArr = []
    const totalAllocation = aa.reduce((total, r) => total + r.AssetBalance, 0)
    if (aa.length !== 0) {
      assetClasses.forEach(a => {
        const subTotal = aa.filter(f => f.AssetClass === a.name).reduce((total, r) => total + r.AssetBalance, 0) * 100 / totalAllocation
        if (subTotal) {
          percentArr.push({ ...a, y: Number(subTotal.toFixed(2)) })
        }
      })
    }
    setAssetAllocation(percentArr)
  }

  const onPortfolioRefresh = async () => {
    await db.remove(db.model.portfolio);
    await db.remove(db.model.portfolioXirr);
    await db.remove(db.model.portfolioPerformance);
    await db.remove(db.model.assetAllocation);
    await db.remove(db.model.transactions)
    loadData();
  }

  const refreshHoldings = async () => {
    var order = {
      "intent": "HOLDINGS_IMPORT",
      "assetConfig": {
        "mfHoldings": false
      }
    }
    var res = await AdminServices.getSmallcaseTransactionId(order).then(res => res)
    try {
      await initializeSmallCase();
      await window.sc.triggerTransaction({ transactionId: res.data.data.transactionId })
        .then((a) => {
          console.log(a)
          if (a.success) {
            try {
              db.get(db.model.user).then(async (p) => {
                var res = await AdminServices.getHoldings(p.userData.id, jwt_decode(a.smallcaseAuthToken).smallcaseAuthId).then((rs) => { return rs })
                console.log(JSON.parse(res.data))

                var res = await AdminServices.updateHoldings(p.userData.id).then(res => res)
                console.log(res)
                onPortfolioRefresh();
                toast.success('Portfolio Updated Successfuly')
              });
            } catch (error) {
              toast.error(error)
            }
          }
        })
        .catch((p) => {
          if(p.message === 'user_cancelled'){
            return
          } else {
            toast.error('Something went wrong! Try again.')
          }
        });
    } catch (e) {
      console.log("chandak", e)
    }
  }

  const mintbox = () => {
    const yourMintboxes = () => {
      return portfolio.MintboxConsolidated?.filter(m => m.GoalId === 0 && m.MintboxBalance !== 0)?.map((data) => (
        <FundCard key={data.MintboxId}
          onClick={() => props.history.push({
            pathname: '/portfolio/mintboxScreen',
            state: { MINTBOXID: data.MintboxId, FUNDDATA: data }
          })}
          fundName={data.MintboxName}
          risk={data.RiskLevel}
          balance={data.MintboxBalance}
          previousBalance={portfolio?.PrevMintboxConsolidated?.find(m => m.MintboxId === data.MintboxId)?.PrevMintboxBalance}
          xirr={Object.values(portfolioXIRR?.MintboxXIRR || {}).find(m => m.MintboxId === data.MintboxId)?.XIRR}
          goalName={data.GoalName}
        />
      ))
    }

    const etfsInMintboxes = () => {
      return portfolio.MintboxETFConsolidated?.filter(e => e.MintboxETFConsolidated !== 0)?.map((data) => (
        <FundCard key={data.FundCode}
          onClick={() => props.history.push({
            pathname: '/portfolio/etfScreen',
            state: { FUNDCODE: data.FundCode, MINTBOXID: 0, TYPE: 'MintboxETFConsolidated', FUNDDATA:data }
          })}
          fundName={data.ETFName}
          AMCName={data.AMCName}
          balance={data.MintboxETFConsolidated}
          previousBalance={portfolio?.PrevMintboxETFConsolidated?.find(m => m.FundCode === data.FundCode)?.PrevMintboxETFConsolidated}
          xirr={Object.values(portfolioXIRR?.MintboxETFConsolidatedXIRR || {}).find(m => m.FundCode === data.FundCode)?.XIRR}
          type={1}
        />
      ))
    }
    const keyTitles2 = [
      { title: "Your mintboxes", view: yourMintboxes() },
      { title: "ETFs in your mintboxes", view: etfsInMintboxes() }
    ];
    return (
      <>
        {
          portfolio.MintboxConsolidated?.some(m => m.MintboxBalance !== 0 && m.GoalId === 0) ?
            <Tabs tabsObject={keyTitles2} color={cssVar.getPropertyValue('--darkGreen')} />
            :
            <NotFound type='mintbox' text="You don't have any investments in mintboxes" buttonText='Invest in mintboxes' onClick={() => { props.history.push({ pathname: "/list", state: { type: 3 } }) }} />
        }
      </>
    );
  }

  const etf = () => {
    return (
      <>
        <div style={{ borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
          <div style={{ padding: "2% 4%", backgroundColor: cssVar.getPropertyValue('--moderateRisk') }}>
            <div className='smallTextLight' style={{ margin: "2px 0" }}>Note: Only ETFs invested in independently (and not as a part of a mintbox) are shown below</div>
          </div>
        </div>
        <div>
          {
            portfolio.ETFConsolidated?.some(e => e.ETFBalance !== 0) ?
              portfolio.ETFConsolidated?.filter(f => f.ETFBalance !== 0)?.map((data) => (
                <FundCard key={data.FundCode}
                  onClick={() => props.history.push({
                    pathname: '/portfolio/etfScreen',
                    state: { FUNDCODE: data.FundCode, MINTBOXID: 0, TYPE: 'ETFConsolidated', FUNDDATA:data }
                  })}
                  fundName={data.ETFName}
                  AMCName={data.AMCName}
                  balance={data.ETFBalance}
                  previousBalance={portfolio?.PrevETFConsolidated?.find(m => m.FundCode === data.FundCode)?.PrevETFBalance}
                  xirr={Object.values(portfolioXIRR?.ETFXIRR || {}).find(m => m.FundCode === data.FundCode)?.XIRR}
                  type={1}
                />
              ))
              :
              <NotFound text="You don't have any investments in ETFs" buttonText='Invest in ETFs' onClick={() => { props.history.push({ pathname: "/list", state: { type: 1 } }) }} />
          }
        </div>
      </>
    )
  }

  const funds = () => {
    return (
      <div style={{ marginTop: "2%" }}>
        {
          portfolio.FundsConsolidated?.some(f => f.MFBalance !== 0) ?
            portfolio.FundsConsolidated?.filter(f => f.MFBalance !== 0)?.map((data) => (
              <FundCard key={data.FundCode}
                onClick={() => props.history.push({
                  pathname: '/portfolio/fundScreen',
                  state: { FUNDCODE: data.FundCode, FOLIO: data.FolioNumber, FUNDDATA:data }
                })}
                fundName={data.SchemeName}
                AMCName={data.AMCName}
                balance={data.MFBalance}
                previousBalance={portfolio?.PrevFundsConsolidated?.find(m => m.FundCode === data.FundCode)?.PrevMFBalance}
                xirr={Object.values(portfolioXIRR?.MFXIRR || {}).find(m => m.FundCode === data.FundCode)?.XIRR}
                type={2}
              />
            ))
            :
            <NotFound text="You don't have any investments in mutual funds" buttonText='Invest in mutual funds' onClick={() => { props.history.push({ pathname: "/list", state: { type: 2 } }) }} />
        }
      </div>
    );
  }

  const goals = () => {
    return (
      <div style={{ marginTop: "2%" }}>
        {
          portfolio.MintboxConsolidated?.some(m => m.GoalId !== 0 && m.MintboxBalance!==0) ?
            portfolio.MintboxConsolidated?.filter(m => m.GoalId !== 0 && m.MintboxBalance!==0).map((goal, i) => {
              const percentComplete = Number(goal.MintboxBalance * 100 / goal.GoalTargetCorpus)
              return (
                <div key={i} style={{ padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light'), display: "flex", gap: "4%" }} onClick={() => { props.history.push({ pathname: '/portfolio/goalScreen', state: { GOALID: goal.GoalId, MINTBOXID: goal.MintboxId, GOALDATA: goal } }) }}>
                  {/* <MintboxIcon risk={goal.RiskLevel} width='40px' height='40px' /> */}
                  <GoalIcon type={goal.GoalType} width='40px' height='40px'/>
                  <div style={{ flex: 1 }}>
                    <div className="header2dark" style={{ display: 'flex', gap: "4%" }}>
                      <div style={{ flex: 1 }}>{goal.GoalName}</div>
                      <div>{CurrencyFormat(goal.MintboxBalance)} <AiOutlineRight size={14} className='secondaryGreen' style={{ marginLeft: '4px' }} /></div>
                    </div>
                    <ProgressBar now={percentComplete} label={<span>&nbsp;&nbsp;{percentComplete.toFixed(0)}% complete</span>}
                      className="progress-bar-goals" style={{ margin: "3% 0" }} />
                    <div className="smallTextLight" style={{ display: 'flex', gap: "4%" }}>
                      <div style={{ flex: 1 }}>Due {new Date(goal.GoalTargetDate).toLocaleDateString('en-us', { year: "numeric", month: "short" })}</div>
                      <div>Target: {CurrencyFormat(goal.GoalTargetCorpus, 'short')}</div>
                    </div>
                  </div>
                </div>
              )
            })
            :
            <NotFound type="goal" text="You don't have any active goal investments" buttonText='Create a Goal' onClick={() => { props.history.push('/Goals') }} />
        }
      </div>
    );
  }

  const keyTitles = [
    { title: "mintboxes", view: mintbox() },
    { title: "ETFs", view: etf() },
    { title: "Mutual funds", view: funds() },
    { title: "Goals", view: goals() },
  ];

  const NotInvested = () => {
    const onLinkDematPress = async() => {
      const res = await AdminServices.getSmallcaseTransactionId({ "intent": "CONNECT" }).then(rs => rs)
      if (res.type) {
          console.log("Trx id =>>", res.data.data.transactionId)
          if (window.sc === undefined) {
              console.log("Initializing Smallcase")
              await initializeSmallCase();
          }
          await window.sc.triggerTransaction({ transactionId: res.data.data.transactionId })
              .then((a) => {
                  console.log(a)
                  if (a) {
                    updateSmallCaseAuthId(user.id, a.smallcaseAuthToken, a.broker).then(res=>{
                      console.log(res)
                      if(res){
                          db.remove(db.model.clientData)
                          toast.success('Demat linked')
                          props.history.goBack()
                      } else {
                          toast.error('Demat already linked with another mintbox account')
                      }
                    })
                  }
              })
              .catch((p) => {
                if(p.message === 'user_cancelled'){
                  return
                } else {
                  toast.error('Something went wrong! Try again.')
                }
              });
      }
  }
    return (<div style={{ margin: "15% 8%", textAlign: 'center' }}>
      <img style={{ width: '120px', height: "120px" }} src='https://d3etfstcuyoos5.cloudfront.net/app-images/web-icons/no-invest.png' alt="" />
      <div className="bigHeader" style={{ marginTop: "24px" }}>No investments found</div>
      <div className="bodyTextLight" style={{ marginTop: "4px", marginBottom: "32px" }}>Just link your demat account to start investing in smart investment portfolios for free!
      </div>
      {
        isDematLinked?
        <button className="solidButton" style={{ background: cssVar.getPropertyValue('--secondaryGreen'), color: cssVar.getPropertyValue('--white'), width: 'auto', paddingLeft: "20px", paddingRight: "20px" }} onClick={()=>props.history.push('/Invest')}>Start investing <AiOutlineRight size={12} style={{ marginLeft: "4px" }} /></button>
        :
        <button className="solidButton" style={{ background: cssVar.getPropertyValue('--secondaryGreen'), color: cssVar.getPropertyValue('--white'), width: 'auto', paddingLeft: "20px", paddingRight: "20px" }} onClick={()=>onLinkDematPress()}>Link demat account <AiOutlineRight size={12} style={{ marginLeft: "4px" }} /></button>
      }
    </div>)
  }

  if (loading) {
    return (
      <div className="mainContainer">
        <MainHeader
          navigation={props.history}
          header="Portfolio"
        />
        <ActivityLoaderMintbox />
      </div>
    );
  } else {
    return (
      <div className="mainContainer">
        <MainHeader
          navigation={props.history}
          header="Portfolio"
        />
        <div className="dataContainer px-0">
          {
            portfolio.PortfolioConsolidated && Number(portfolio.PortfolioConsolidated[0].PortfolioBalance) !== 0 ?
              <>
                <div style={{ display: "flex", alignItems: 'center', margin: "4% 4% 0", alignItems: "center" }}>
                  <div style={{ flex: '1', fontSize: '16px', fontWeight: "500", color: '#333333' }}>
                    Summary
                  </div>
                  {/* <span onClick={() => refreshHoldings()} className='greenChipHeader'>
                    Sync with demat
                  </span> */}
                </div>
                <HomePortfolioCard
                  balance={portfolio.PortfolioConsolidated[0].PortfolioBalance}
                  previousBalance={portfolio.PrevPortfolioConsolidated[0].PrevPortfolioBalance}
                  xirr={portfolioXIRR.PortfolioXIRR?.XIRR}
                  onRefresh={() => onPortfolioRefresh()}
                  growthChartData={portfolioPerformance?.Portfolio?.map(d => [new Date(d["NavDateStr"]).getTime(), d["Balance"]])}
                  pieChartData={assetAllocation}
                />
                <div className="header1dark" style={{ margin: "4% 0 0 4%" }}>Holdings</div>
                <Tabs tabsObject={keyTitles} color={cssVar.getPropertyValue('--darkGreen')} />
              </>
              :
              <NotInvested />
          }
        </div>
      </div>
    );
  }
};

export default PortfolioScreen;