import React from 'react'
import cssVar from "views/mobileView/styles";
import { RiskColor } from './RiskFunctions';

export default function MintboxIcon({risk,height,width}) {
  return(
    <div style={{ display:'flex', border: "1px solid " + cssVar.getPropertyValue("--light"),padding:`calc(${height}*10/100) 0`, borderRadius: '4px', height, width, backgroundColor: RiskColor(risk)}}>
        <img src='https://d3etfstcuyoos5.cloudfront.net/app-images/wealth-creators.png' style={{height: `calc(${height}*80/100)`,margin:'auto'}}/>
    </div>
  )
}